<template>
  <div style="background-color :#F4F4F4;"
    class="sm:grid grid-cols-11 sm:mt-40 mt-20   top-0 left-0 right-0 sm:h-32 h-20 shadow-md  sm:block">

    <div
      class=" h-full w-full resize rounded-md border-indigo-200 border-r-indigo-500 hidden sm:block background-f4 col-span-3">

      <div class="grid  mt-6">
        <div class="">
          <h1 class="sm:text-4xl text-2xl font-bold ml-10 font-Montserrat"
            :class="[showbroker ? 'col-span-4' : 'col-span-8']">{{ title }}</h1>

          <div class=" flex ml-10 mt-3">
            <h1 class=" text-lg">{{ getUserName }} </h1><br />
            <h3 class="text-lg  ">
              {{ getUserReference }}
            </h3>

          </div>


        </div>



      </div>
    </div>
    <div
      class=" h-full w-full resize rounded-md border-indigo-200 border-r-indigo-500 hidden sm:block background-f4 col-span-2  mt-5">

      <div class="grid  mt-3">
        <div class="">
          <h3 class="sm:text-2xl  font-bold ml-10 font-Montserrat" :class="[showbroker ? 'col-span-4' : 'col-span-8']">
            Value</h3>

          <div class=" flex ml-10 mt-3 ">
            <div v-for="total in getTotals" v-bind:key="total.currency" class="flex">
              <div class="">
                <span class="text-lg">{{
                  currencyFormatter(total.total)
                }}</span><br clear="all" />
              </div>

              <div class="">
                <span class="text-lg">{{
                  total.currency
                }}</span>
              </div>
            </div><br />


          </div>


        </div>



      </div>
    </div>
    <div
      class=" h-full w-full resize rounded-md border-indigo-200 border-r-indigo-500 hidden sm:block background-f4 col-span-2  mt-5">

      <div class="grid  mt-3">
        <div class="">
          <h3 class="sm:text-2xl  font-bold ml-10 font-Montserrat" :class="[showbroker ? 'col-span-4' : 'col-span-8']">
            Grow</h3>

          <div class=" flex ml-10 mt-3 ">
            <div v-for="total in getTotals" v-bind:key="total.currency" class="flex">
              <div class="">
                <span :class="[
                  total.changePrice >= 0
                    ? 'oeno_portfolio_price_up'
                    : 'oeno_portfolio_price_down',
                ]" class="text-lg">{{ getChangeTotal(total) }}</span>
              </div>
            </div>

          </div>


        </div>



      </div>
    </div>
    <div
      class=" h-full w-full resize rounded-md border-indigo-200 border-r-indigo-500 hidden sm:block background-f4 col-span-2  mt-5">

      <div class="grid  mt-3">
        <div class="">
          <h3 class="sm:text-2xl  font-bold ml-10 font-Montserrat" :class="[showbroker ? 'col-span-4' : 'col-span-8']">
            Fund Value</h3>

          <div class=" flex ml-10 mt-3 ">

          </div>
        </div>
      </div>
    </div>

    <div class=" h-36 w-auto col-span-4   sm:block">




      <!-- <div class="grid grid-cols-12 gap-6 mt-5">
          <p class="font-Montserrat font-medium text-sm  col-span-5  mt-5" v-if="showbroker == true"><i>
              The prices shown are estimates and could change when you come to
              sell your wines.<br />
              Your broker : {{ getUserBroker }}
            </i> </p>
          <div class="grid justify-items-center cols-span-2 space-y-4 h-4/5 ">
            <button style="border-color: #000000" class="w-48 h-10 oeno_button_white" @click="buywines('oenohouse')" v-if="showaction == true">BUY
              TO
              CONSUME</button>
            <button class="w-72 h-10 oeno_button_black " @click="buywines('future')" v-if="showaction == true" >ADD
              TO INVESTMENT ACCOUNT</button>
            <button class="sm:w-72 sm:h-10 mt-20 w-20 h-8 oeno_button_black  sm:mt-10" @click="$router.go(-1)"  v-if="showback == true">GO BACK</button>
            <button class="sm:w-72 sm:h-10  w-24 h-8  oeno_button_white mt-10" @click="$router.push({name: 'page-portfolio'})"  v-if="showportfolio == true">CHECK YOU PORTFOLIO</button>
          </div>
        </div> -->
    </div>

  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "SecondaryNavbar",
  props: {
    title: {
      type: String,
      default: "",
    },
    showbroker: {
      type: Boolean,
      default: true,
    },
    showaction: {
      type: Boolean,
      default: true,
    },
    showback: {
      type: Boolean,
      default: false,
    },
    showportfolio: {
      type: Boolean,
      default: false,
    },
  },
  computed: {

    getUserName() {
      var userName = "";
      if (this.$store.state.user) {
        userName =
          this.$store.state.user.firstname +
          " " +
          this.$store.state.user.surname;
      }
      return userName;
    },
    getUserReference() {
      var reference = "";
      if (this.$store.state.user) {
        reference = "(" + this.$store.state.user.unique_number + ")";
      }
      return reference;
    },
    getUserBroker() {
      var broker = "";
      if (this.$store.state.user) {
        broker =
          this.$store.state.user.brokerName +
          " " +
          this.$store.state.user.brokerEmail;
      }
      return broker;
    },
    getTotals() {
      const values = [];

      if (this.$store.state.portfolio) {
        this.$store.state.portfolio.forEach((investment) => {
          investment.items.forEach((item) => {
            var itemPrice = this.getValuesForItem(investment, item);

            var pos = -1;
            for (let index = 0; index < values.length; index++) {
              if (values[index].currency == itemPrice.currency) {
                pos = index;
              }
            }

            if (pos > -1) {
              values[pos].total =
                values[pos].total + parseFloat(itemPrice.total);
              values[pos].changePrice =
                values[pos].changePrice + parseFloat(itemPrice.changePrice);
            } else {
              values.push({
                currency: itemPrice.currency,
                changePrice: parseFloat(itemPrice.changePrice),
                total: itemPrice.total,
              });
            }
          });
        });
      }
      return values;
    }
  },
  methods: {

    getChangeTotal(total) {
      var changePerc =
        (total.changePrice / (total.total - total.changePrice)) * 100;

      return (
        this.currencyFormatter(total.changePrice) +
        "(" +
        changePerc.toFixed(2) +
        "%)"
      );
    },

    getValuesForItem(investment, wine) {
      var isInLastSixMonths = "N";
      var orderDate = moment(investment.order_date);
      var sixMonthsAgo = moment().subtract(6, "months");
      if (sixMonthsAgo < orderDate) {
        isInLastSixMonths = "Y";
      }

      var buytotal =
        wine.quantity * wine.wines.units_per_case * wine.bottle_price;

      var total =
        parseInt(
          parseFloat(
            wine.quantity *
            wine.wines.units_per_case *
            wine.livePrices.data["price-average"]
          ) * 100
        ) / 100;

      if (wine.livePrices.data["price-average"] == 0) {
        total = buytotal;
      }

      if (isInLastSixMonths == "Y") {
        if (total < buytotal) {
          total = buytotal;
        }
      }

      var changePerc =
        ((parseFloat(total) - parseFloat(buytotal)) / parseFloat(buytotal)) *
        100;

      var changePrice = parseFloat(total) - parseFloat(buytotal);

      return {
        changePerc: changePerc,
        changePrice: changePrice,
        total: total,
        currency: investment.currency,
      };
    },

    buywines(type) {
      this.$store.dispatch("storeMarketData", type).then(() => {
        this.$router.push(
          {
            name: 'page--stock',
            params: { type: type },
          })
      });
    },
    sellwine(id, orderId) {
      this.$router.push({
        name: 'page--sell', params: { id: id, orderId: orderId }
      });
    },
  }
}
</script>

<style></style>